.App {
  text-align: left;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  grid-auto-rows: minmax(100px,auto);
}

#example{

}

#instruction{
  grid-row: 2;
  grid-column: 3;
  font-family: sans-serif;
  line-height: 1;
}

#dfa1_ae_wrapper{
  grid-row: 2;
  grid-column: 1;
}

#dfa2_ae_wrapper{
  grid-row: 2;
  grid-column: 2;
}

#submit_bt{
  grid-row: 1;
  grid-column: 3;
}

